// @flow
import _get from 'lodash/get';
import type { Offer as TOffer } from 'types/Offer';
import { FUNDING_PURCHASE_TYPES } from 'shared/Consts';
import _isEmpty from 'lodash/isEmpty';
import {
  displayFurniture,
  getBuyerName,
  getSellerName,
  getFullName,
} from '../../../helpers/Offer';
import { formatPrice } from 'helpers/number';

type TField = {
  label: string,
  value: string | number,
};

export const getDetailFields = (
  offer: TOffer,
  isSeller: Boolean,
): Array<TField> => {
  if (!offer || _isEmpty(offer)) return [];

  const funding = _get(offer, 'Funding.PurchaseType', '');

  const individualName = isSeller
    ? {
        label: 'Buyer Name',
        value: getBuyerName(offer),
      }
    : {
        label: 'Seller Name',
        value: getSellerName(offer),
      };
  const agentName = isSeller
    ? {
        label: 'Buyer Agent Name',
        value: getFullName(_get(offer, 'AgentInfo', {})),
      }
    : { label: 'NILE Agent Name', value: 'Khaled Yatim' };

  return [
    { label: 'Offer No', value: _get(offer, 'OfferNo', '') },
    individualName,
    agentName,
    {
      label: 'funding',
      value: funding,
    },
    {
      label: 'Loan Type',
      value:
        funding === FUNDING_PURCHASE_TYPES.cash
          ? 'N/A'
          : _get(offer, 'Funding.LoanType', ''),
    },
    {
      label: 'Down Payment',
      value: formatPrice(_get(offer, 'Funding.DownPayment', '')),
    },
    {
      label: 'Proof of funds',
      value:
        funding === FUNDING_PURCHASE_TYPES.cash
          ? formatPrice(_get(offer, 'Funding.CashProofOfFundsAmount', ''))
          : formatPrice(_get(offer, 'Funding.FinancingProofOfFundsAmount', '')),
    },
  ];
};

export const getFurnitureField = (offer: TOffer): TField => {
  return {
    label: 'Furniture',
    value: displayFurniture(
      _get(offer, 'Offer.Furniture', ''),
      _get(offer, 'Offer.FurnitureOther', ''),
    ),
  };
};
